.transition-all {
  transition-property: all;
}

.ease-in-out {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.duration-300 {
  transition-duration: 300ms;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes ping {
  75%, 100% {
    transform: scale(2);
    opacity: 0;
  }
}

@keyframes pulse {
  50% {
    opacity: .5;
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50% {
    transform: none;
    animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

@media (min-width: 640px) {
  .sm\:bg-primary {
    background-color: var(--color-bg-primary);
  }

  .sm\:inline-block {
    display: inline-block;
  }

  .sm\:hidden {
    display: none;
  }

  .sm\:flex-row {
    flex-direction: row;
  }

  .sm\:items-center {
    align-items: center;
  }

  .sm\:h-auto {
    height: auto;
  }

  .sm\:mt-0 {
    margin-top: 0;
  }

  .sm\:ml-3 {
    margin-left: 0.75rem;
  }

  .sm\:max-w-2xl {
    max-width: 42rem;
  }

  .sm\:min-w-sm {
    min-width: 24rem;
  }

  .sm\:min-w-xl {
    min-width: 36rem;
  }

  .sm\:min-w-2xl {
    min-width: 42rem;
  }

  .sm\:px-0 {
    padding-left: 0;
    padding-right: 0;
  }

  .sm\:static {
    position: static;
  }

  .sm\:text-justify {
    text-align: justify;
  }

  .sm\:w-64 {
    width: 16rem;
  }

  .sm\:w-auto {
    width: auto;
  }

  .sm\:transition-none {
    transition-property: none;
  }
}

